.splash-screen {
  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-animation: bg 7.2s linear forwards;
          animation: bg 7.2s linear forwards;
}

.splash-screen--message {
  font-size: 20px;
  max-width: 250px;
  text-align: center;
  font-weight: 300;
  opacity: 0;
  -webkit-animation: message 3.5s linear forwards;
          animation: message 3.5s linear forwards;
}

.splash-screen .logo svg {
  width: 250px;
  height: 250px;
  margin-bottom: 3rem;
}

.path {
  stroke-width: 3;
  stroke: #feffff;
}

.path__3,
.path__4,
.path__1,
.path__2,
.path__7,
.path__11,
.path__12,
.path__14 {
  stroke-dasharray: 250;
  stroke-dashoffset: 250;
  -webkit-animation: dash250 4s linear alternate infinite;
          animation: dash250 4s linear alternate infinite;
}

.path__6,
.path__15,
.path__16,
.path__5,
.path__9,
.path__10,
.path__19,
.path__8,
.path__17,
.path__18,
.path__24,
.path__25,
.path__23,
.path__21,
.path__22 {
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  -webkit-animation: dash400 3.5s linear alternate infinite;
          animation: dash400 3.5s linear alternate infinite;
}

.path__20 {
  stroke-dasharray: 600;
  stroke-dashoffset: 600;
  -webkit-animation: dash600 3.5s linear alternate infinite;
          animation: dash600 3.5s linear alternate infinite;
}

.path__13 {
  stroke-dasharray: 850;
  stroke-dashoffset: 850;
  -webkit-animation: dash850 3.5s linear alternate infinite;
          animation: dash850 3.5s linear alternate infinite;
}

@-webkit-keyframes bg {
  0% {
    background-color: #2b7a78;
  }
  100% {
    background-color: #3aafa9;
  }
}

@keyframes bg {
  0% {
    background-color: #2b7a78;
  }
  100% {
    background-color: #3aafa9;
  }
}

@-webkit-keyframes message {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes message {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes dash250 {
  0% {
    stroke-dashoffset: 250;
    opacity: 0.15;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@keyframes dash250 {
  0% {
    stroke-dashoffset: 250;
    opacity: 0.15;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@-webkit-keyframes dash400 {
  0% {
    stroke-dashoffset: 400;
    opacity: 0.1;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@keyframes dash400 {
  0% {
    stroke-dashoffset: 400;
    opacity: 0.1;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@-webkit-keyframes dash600 {
  0% {
    stroke-dashoffset: 600;
    opacity: 0.1;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@keyframes dash600 {
  0% {
    stroke-dashoffset: 600;
    opacity: 0.1;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@-webkit-keyframes dash850 {
  0% {
    stroke-dashoffset: 850;
    opacity: 0.1;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

@keyframes dash850 {
  0% {
    stroke-dashoffset: 850;
    opacity: 0.1;
  }
  25% {
    opacity: 0.3;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

