* {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  color: #feffff;
  font-size: 16px;
  font-family: "Raleway", sans-serif;
  background-color: #3aafa9;
}

.App {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  max-width: 700px;
  margin: 0 auto;
  padding: 2rem;
  font-weight: 300;
}

h1 {
  font-size: 4.5em;
  font-weight: 700;
  text-transform: uppercase;
  color: #2b7a78;
  letter-spacing: -0.025em;
  margin: 0;
}

h2 {
  font-weight: 300;
  font-size: 1.5em;
  letter-spacing: 0.05em;
}

/* Result Component */
.result {
  padding: 0;
}

.result__type {
  font-size: 3.5rem;
  font-weight: 700;
  color: #2b7a78;
  letter-spacing: -0.05em;
  word-spacing: 0.05em;
  text-align: center;
}

.result__img {
  padding: 2rem 0;
}

.result__img img {
  width: 100%;
}

.results__table {
  font-size: 1.125em;
  line-height: 1.5em;
}

/* Quiz Component */
.answerOptions {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* QuestionCount Component */
.questionCount {
  padding: 0;
  font-size: 14px;
  font-weight: 300;
}

/* Question Component */
.question {
  margin: 0;
  padding: 1rem 0 3rem;
}

/* AnswerOption Component */
.answerOption {
  border-radius: 3px;
  margin: 30px 0 40px;
  background-color: #eefbfe;
  box-shadow: 0 0.25rem 1rem rgba(48, 55, 66, 0.15);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
}

.answerOption:hover {
  background-color: #d2f5f3;
}

.radioCustomButton {
  position: absolute;
  width: auto;
  opacity: 0;
}

.radioCustomButton,
.radioCustomLabel {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  font-weight: 400;
}

.radioCustomLabel {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 1.5rem 2.5rem;
  font-size: 16px;
  line-height: 1.5;
  color: #17252a;
}

/* Animation */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 5s ease-in-out;
}

.fade-leave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.fade-appear {
  opacity: 0.2;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

